import utils from './utils';

function Camera() {
  const PLUGIN_NAME = 'Camera';

  /**
   * Uses Camera mobile plugin
   *
   * @see https://bitbucket.org/backbase/mobile-plugin-camera
   *
   * @name Camera
   * @type {Object}
   */
  return {
    /**
     * Method to get the image from camera
     *
     * @name Camera#getImageFromCamera
     * @type {function}
     * @param {number} maxWidth Maximum width
     * @param {number} maxHeight Maximum height
     * @return {Promise}
     */
    getImageFromCamera(maxWidth, maxHeight) {
      return new Promise((resolve, reject) => {
        utils.getMobilePlugin(PLUGIN_NAME)
          .getImageFromCamera(resolve, reject, maxWidth, maxHeight);
      });
    },

    /**
     * Method to get the image from gallery
     *
     * @name Camera#getImageFromGallery
     * @type {function}
     * @param {number} maxWidth Maximum width
     * @param {number} maxHeight Maximum height
     * @return {Promise}
     */
    getImageFromGallery(maxWidth, maxHeight) {
      return new Promise((resolve, reject) => {
        utils.getMobilePlugin(PLUGIN_NAME)
          .getImageFromGallery(resolve, reject, maxWidth, maxHeight);
      });
    },
  };
}

export default Camera;



