import utils from './utils';

function ActivityIndicator() {
  const PLUGIN_NAME = 'ActivityIndicator';

  /**
   * Uses ActivityIndicator mobile plugin
   *
   * @see https://bitbucket.org/backbase/mobile-plugin-activity-indicator
   *
   * @name ActivityIndicator
   * @type {object}
   */
  return {
    /**
     * Shows activity indicator with given message
     * @name ActivityIndicator#show
     * @type {function}
     * @param {String} [message]
     * @return {Promise}
     */
    show(message) {
      return new Promise((resolve, reject) => {
        utils.getMobilePlugin(PLUGIN_NAME).show(resolve, reject, message || '');
      });
    },

    /**
     * Hides activity indicator
     * @name ActivityIndicator#hide
     * @type {function}
     * @return {Promise}
     */
    hide() {
      return new Promise((resolve, reject) => {
        utils.getMobilePlugin(PLUGIN_NAME).hide(resolve, reject);
      });
    },
  };
}

export default ActivityIndicator;



